html.security #business-owner-app main .HomeContainer .Home .pillars #ForgotPassword a,
#business-owner-app main .HomeContainer .Home html.security .pillars #ForgotPassword a,
html.security #vendor-app main .HomeContainer .Home .pillars #ForgotPassword a,
#vendor-app main .HomeContainer .Home html.security .pillars #ForgotPassword a {
  color: var(--theme-link-colour);
}
html.security .login-image {
  display: flex;
  background-color: var(--header-colour);
  padding-top: 5px;
  padding-bottom: 5px;
}
html.security .security {
  /* Full height */
  height: 100%;
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  /**
  Additionally, we style the buttons with btn-primary
  **/
}
html.security .security input.action {
  background-color: var(--header-colour);
}
html.security .security input[name=Email]:focus, html.security .security input[name=Password]:focus {
  border-color: var(--header-colour);
}
html.security .security input[type=checkbox] {
  display: none;
}
html.security .security .checkbox label {
  display: inline-block;
  position: relative;
  padding-left: 25px;
  font-size: 16px;
  line-height: 20px;
  margin: 5px;
}
html.security .security .checkbox label:before {
  line-height: 20px;
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
  position: absolute;
  left: 0;
  background-color: #ffffff;
  border: 1px solid #666666;
}
html.security .security input[type=checkbox]:checked + label:before,
html.security .security .checkbox label:hover:before {
  content: "";
  color: white;
  text-align: center;
  line-height: 16px;
  background-color: var(--header-colour);
}
/**
END Login background image styling
**/